.box{
    width: 800px;
    height: 600px;
    background: #4f81bd;
    margin: 0 auto;
    overflow: hidden;
}
.box-header{
    width: 500px;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 20px;
}
.inp{
    width: 400px;
    height: 30px;
    background: #000;
    color: #fff;
}
.btn{
    width: 70px;
    height: 36px;
    background: green;
    color: #fff;
    border: 2px solid #27405e;
    border-radius: 10px;
}
.box ul{
    width: 500px;
    height: 400px;
    background: #953735;
    margin: 0 auto;
    border: 2px solid #27405e;
    overflow: scroll;
    overflow-x: hidden;
    overflow-y: auto;
}
.box ul li{
    width: 400px;
    height: 45px;
    margin: 10px auto;
    background:#4f81bd;
    padding: 10px;
    border: 2px solid #27405e;
    color: #fff;
}
.btn1{
    border: 2px solid #27405e;
    width: 100px;
    height: 30px;
    background: red;
    color: #fff;
    margin-top: -4px;
}