.box{
    width: 800px;
    height: 600px;
    margin: 0 auto;
    background: #4f81bd;
}
.cen{
    width: 600px;
    height: 500px;
    background: #953735;
    margin: 0 auto;
    border: 2px solid #953735;
}
.maintop{
    width: 500px;
    height: 40px;
    background: #4f81bd;
    margin: 10px auto;
    line-height: 40px;
    color: #fff;
    text-align: center;
    & span{
        margin-left: 160px;
    }
    & button{
        width: 80px;
        height: 30px;
        background: rgb(255, 0, 242);
        border: 2px solid #953735;
        margin-right: 10px;
        color: #fff;
    }
}
.main-buttom{
    width: 500px;
    height: 400px;
    margin: 0 auto;
    & div{
        width: 200px;
        height: 350px;
        background: #4f81bd;
    }
}
