.shou-main {
  background: #f2f2f2;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  .shou-header {
    ul {
      margin: 0;
      padding: 0;
      margin: 15px;
      .shou-header-item {
        width: 265px;
        height: 68px;
        background: white;
        text-align: center;
        line-height: 68px;
        a {
          text-decoration: none;
          color: black;
        }
      }
    }
  }
  .col {
    margin-bottom: 15px;
    background-color: #fff;
    .col-header {
      height: 42px;
      line-height: 42px;
      padding: 0 15px;
      border-bottom: 1px solid #f6f6f6;
      font-size: 14px;
    }
    .col-body {
      padding: 10px 15px;
      line-height: 24px;
    }
    .col-table {
      border-spacing: 0px;
      tr {
        td {
          border: 1px solid #e6e6e6;
          padding: 6px 20px;
        }
        .left-td {
          width: 20%;
        }
        .right-td {
          width: 80%;
          padding-right: 800px;
        }
      }
    }
  }
}
