.layui-header{
    height: 50px;
    box-sizing: border-box;
    background-color: #001529;
}
.layui-header .layui-logo{
    line-height: 50px;
    text-align: left;
    position: relative;
}
.layui-logo{
    left: 0;
    top: 0;
    width: 200px;
    height: 100%;
    color: #009688;
    font-size: 16px;
}
.layui-logo-toggle{
    position: absolute;
    left: 0;
    top: 0;
    display: block;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    color: #fff;
    cursor: pointer;
}
.layui-logo-toggle:hover{
    background-color: black;
    color: #009688;
}
.layui-logo-brand:hover{
    background-color: black;
}
.layui-logo-brand a:hover{
    color: #009688;
}
.layui-logo-brand{
    padding-left: 50px;
}
.layui-logo-brand a{
    display: block;
    padding-left: 23px;
    color: #fff;
    text-decoration: none;
}
.layui-header  .layui-right{
    right: 20px;
}
.layui-right a{
    color: #fff;
}
.layui-right a.child{
    padding-right: 35px;
}
.layui-nav-img{
    width: 30px;
    height: 30px;
    margin-right: 10px;
    border-radius: 50%;
    vertical-align: middle;
}
.layui-right span{
    line-height: 50px;
}