.main-box{
    width:600px;
    height: 500px;
    background: blue;
    margin: 30px auto;
    overflow: hidden;
    color: #fff;
    .headbox{
        width: 400px;
        height: 80px;
        margin: 30px auto;
        button{
            width: 150px;
            height: 50px;
            background: green;
            color: #fff;
        }
    }
    .conbox{
        width: 450px;
        height: 350px;
        margin: 0 auto;
        div{
            width: 200px;
            height: 350px;
            background: rgb(160, 19, 179);
        }
        .conbox1{
            button{
                width: 160px;
                height: 40px;
                background: blue;
                margin: 35px 20px;
            }
        }
        .conbox2{
            line-height: 350px;
            text-align: center;
        }
    }
}